.checkbox {
  position: relative;
  margin-bottom: 1rem;
}

.checkbox__input {
  -webkit-appearance: none;
  appearance: none;
  position: absolute;
}

.checkbox__label {
  padding-left: 25px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #2e3a59;
  cursor: pointer;
}

.checkbox__label:before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  border: 1px solid #ccc;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transition: background 0.1s linear, border 0.1s linear;
}

.checkbox__label:after {
  content: "";
  display: block;
  width: 12px;
  height: 12px;
  background: url("../svg/components/check.svg") no-repeat;
  background-size: 12px 12px;
  opacity: 0;
  position: absolute;
  top: 2px;
  left: 2px;
  z-index: 2;
  transition: opacity 0.1s linear;
}

.checkbox__input:checked + .checkbox__label:before {
  background-color: #d5ba64;
  border-color: #d5ba64;
}

.checkbox__input:checked + .checkbox__label:after {
  opacity: 1;
}

.c_select {
  padding: 10px 17px;
  min-height: 36px;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  width: 100%;
  position: relative;
}

.c_select .c_select-placeholder {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 135%;
  color: #1a1c22;
}

.c_select:after {
  display: none !important;
}

.c_select .c_select-placeholder:after {
  content: "";
  background: url("../svg/components/arrow-down.svg") no-repeat;
  transition: all 0.5s ease;
  position: absolute;
  top: 50%;
  right: 8px;
  transform: translate(0, -50%);
  height: 5px;
  width: 9px;
}

.c_select.c_select-item-active .c_select-placeholder:after {
  transition: all 0.5s ease;
  transform: translate(0, -50%) rotate(180deg);
}

.c_select .c_select-block {
  bottom: auto;
  top: calc(100% + 3px);
  position: absolute;
  left: 0;
  z-index: 5;
}

.c_select.c_select-item-active .c_select-block {
  padding: 20px;
  width: 100%;
  height: auto;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.c_select .c_select-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  height: 145px;
  overflow-y: auto;
}

.c_select .c_select-wrapper::-webkit-scrollbar {
  width: 27px;
}

.c_select .c_select-wrapper::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 10px 10px #808080;
  box-shadow: inset 0 0 10px 10px #808080;
  border: solid 12px transparent;
}

.c_select .c_select-wrapper::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 10px 10px #000;
  box-shadow: inset 0 0 10px 10px #000;
  border: solid 10px transparent;
}

.c_select .c_select-element {
  width: 100%;
  padding: 3px 0;
  background: 0 0;
  font-family: "NunitoSans";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 30px;
  color: #000000;
  border: none;
  margin: 0 0 5px 0;
  text-align: left;
  cursor: pointer;
}

.c_select .c_select-element-active {
  background: rgba(0, 0, 0, 0.03);
}

.accordion {
  padding: 40px 0 55px 0;
  position: relative;
  z-index: 5;
}

.accordion .containers {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.accordion__t {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 35px;
  line-height: 42px;
  color: #282828;
}

.accordion__a {
  margin: 13px 0 0 0;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: rgba(0, 0, 0, 0.8);
}

.accordion__w {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
  margin: 55px 0 0 0;
}

.accordion__block {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.accordion__header {
  min-height: 75px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  padding: 26px 36px 26px 36px;
  width: 100%;
  display: grid;
  grid-template-columns: 20fr 1fr;
  align-items: center;
  gap: 30px;
  cursor: pointer;
  transition: all 0.5s ease;
}

.accordion__title {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 100%;
  color: #000000;
  transition: all 0.5s ease;
}

.accordion__close {
  width: 22px;
  height: 22px;
  background: transparent;
  cursor: pointer;
  transform: rotate(0deg);
  transition: transform 0.5s ease;
}

.accordion__content {
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: all 0.5s ease;
  background: #ffffff;
  border: 1px solid transparent;
  box-shadow: 0px 0px 30px transparent;
  border-radius: 0 0 10px 10px;
}

.accordion__content p {
  padding: 15px 30px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: rgba(0, 0, 0, 0.8);
  transition: all 0.5s ease;
}

.accordion__p {
  margin: 45px auto 0 auto;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: black;
  background: #ececec;
  padding: 14px 30px;
  cursor: pointer;
  width: max-content;
}

.active .accordion__title {
  color: #fff;
}

.active .accordion__header {
  background: #5450a1;
  border-radius: 10px 10px 0 0;
}

.active .accordion__close {
  transform: rotate(180deg);
}

.active .accordion__close svg path {
  fill: #fff;
}

.active .accordion__content {
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.05);
  height: auto;
  transition: all 0.5s ease;
}

.banners__slider {
